"use client";

import React from "react";
import Link from "next/link";

export default function S10() {
  return (
    <section
      id="s10"
      className="py-12 md:py-20 bg-white w-full min-h-[90vh]"
    >
      <div className="container mx-auto px-4 sm:px-8 md:px-12 lg:px-20">
        <div className="grid lg:grid-cols-2 gap-10 md:gap-16 items-center">
          
          {/* Left Image Section */}
          <div className="flex justify-center relative">
            <div className="rounded-xl overflow-hidden shadow-md w-full max-w-sm sm:max-w-md lg:max-w-none h-auto">
              <img
                src="/WeARE.png"
                alt="Appointment Generation Graphic"
                className="w-full h-full object-cover"
              />
            </div>
          </div>

          {/* Right Text Section */}
          <div className="space-y-6">
            <h2 className="text-3xl sm:text-4xl md:text-5xl font-bold text-gray-800">
              Appointment <span className="text-gray-500">Generation</span>
            </h2>

            <p className="text-base sm:text-lg leading-relaxed text-gray-600">
              We identify decision-makers, validate their interest, and set up confirmed meetings with your sales team—removing the friction of cold outreach. Your sales team stays focused on closing deals, while we handle the time-consuming prospecting work.
            </p>

            {/* CTA Button */}
            <div>
              <Link
                href="/contact"
                className="relative px-8 py-3 sm:px-10 sm:py-3.5 font-semibold text-white bg-gray-800 rounded-md overflow-hidden group hover:bg-gray-700 transition-colors duration-300"
              >
                Book Free Growth Call
              </Link>
            </div>
          </div>

        </div>
      </div>
    </section>
  );
}
